<template>
    <div>
        <div class="cells">
            <mt-cell title="车牌号">
                <input type="text" placeholder="请输入车牌号" v-model="info.carno" class="my-input">
            </mt-cell>
            <mt-cell title="新车未上牌">
                <mt-switch v-model="info.newCar"></mt-switch>
            </mt-cell>
            <mt-cell title="车主姓名">
                <input type="text" placeholder="请输入车主姓名" v-model="info.name" class="my-input">
            </mt-cell>
        </div>
        <div class="cells">
            <mt-cell title="车辆识别代号">
                <input type="text" placeholder="请输入车辆识别代号" v-model="info.identification" class="my-input">
            </mt-cell>
            <mt-cell title="发动机号码">
                <input type="text" placeholder="请输入发动机号码" v-model="info.engineNo" class="my-input">
            </mt-cell>
            <mt-cell title="注册日期" is-link  @click.native="openPicker">
                <div>{{info.registeDate}}</div>
            </mt-cell>
        </div>
        <div class="cells">
            <mt-cell title="行驶区域">
                <input type="text" placeholder="请选择行驶区域" v-model="info.city" class="my-input">
            </mt-cell>
        </div>
        <mt-datetime-picker
                :endDate="endDate"
                v-model="dateTime"
                type="date"
                ref="picker2"
                @confirm="handleConfirm">
        </mt-datetime-picker>
    </div>
</template>

<script>
    import moment from 'moment';
    export default {
        name: "quoteform",
        data(){
            return{
                newCar: true,
                dateTime: null,
                endDate: new Date(),

                info:{
                    carno:'浙AD4567',
                    name:'林夕',
                    identification:'123',
                    engineNo:'234455666',
                    registeDate:'2018-01-01',
                }
            }
        },
        methods: {
            openPicker() {
                this.$refs.picker2.open();
            },
            handleConfirm(data) {
                this.info.registeDate = moment(data).format('YYYY-MM-DD');
            },
        }
    }
</script>

<style lang="scss" scoped>
    .my-input{
        text-align: right;
        color:#333;
        border:0;
    }
    .cells{
        margin-bottom: 15px;
    }
</style>